.graphics.about {
	background: linear-gradient(to bottom, $purple 0%, $celeste 60%, $blue 100%);

	main {
		position: relative;
		z-index: 20;
		.columns .column:nth-child(2) {
			display: flex;
			justify-content: center;
			.card {
				width: rem(400px);
				background-color: white;
				color: black;
				border-radius: rem(10px);
				padding: rem(40px) rem(20px);
				text-align: left;
				box-shadow: 0 0 rem(50px) transparentize($black, .9), 0 0 rem(15px) transparentize($black, .8), 0 0 rem(5px) transparentize($black, .9);
				.top {
					text-align: center;
					& > img {
						border-radius: 100%;
						height: rem(100px);
						width: rem(100px);
						margin-bottom: rem(20px); }
					h2 {
						font-size: rem(24px);
						font-weight: normal;
						& ~ div {
							display: flex;
							justify-content: center;
							margin-top: rem(10px);
							margin-bottom: rem(20px);
							p {
								margin: 0 rem(5px);
								font-style: italic;
								width: fit-content; }
							img {
								height: rem(19px); } } } }
				.middle, .bottom {
					border-top: rem(1px) solid transparentize($black, .8);
					padding-top: rem(20px);
					h3 {
						font-size: rem(16px);
						font-weight: bold;
						margin-bottom: rem(20px); } }
				.middle {
					padding-bottom: rem(15px);
					p {
						margin: 0 rem(10px) rem(5px) 0;
						font-size: rem(16px);
						color: white;
						background-color: $aboutCardDivider;
						border-radius: rem(3px);
						padding: rem(5px);
						width: fit-content;
						line-height: 1.2; } }
				.bottom {
					div {
						display: flex;
						flex-direction: row;
						a {
							display: flex;
							height: rem(30px);
							width: rem(30px);
							border-radius: 100%;
							justify-content: center;
							align-items: center;
							margin-right: rem(10px);
							&.github {
								background-color: $github; }
							&.linkedin {
								background-color: $linkedin; }
							&.dribble {
								background-color: $dribble; }
							&.instagram {
								background-color: $instagram; }
							i {
								color: $white;
								font-size: rem(18px); } } } } } }
		.action {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			margin-top: rem(200px);
			margin-bottom: rem(300px);
			h2 {
				font-size: rem(24px);
				font-weight: 500;
				text-shadow: 0 rem(2px) rem(25px) transparentize($black, .85), 0 rem(1px) rem(2px) transparentize($black, .65);
				margin-bottom: rem(20px); }
			button {
				border: 0;
				border-radius: rem(6px);
				background-color: white;
				padding: rem(15px) rem(80px);
				box-shadow: 0 rem(4px) rem(4px) transparentize($black, .75), 0 rem(2px) rem(2px) transparentize($black, .75);
				a {
					display: flex;
					img {
						height: rem(20px); }
					p {
						font-size: rem(18px);
						margin: 0 rem(5px); } } } } }

	.waves {
		.wave1 {
			bottom: 0; }
		.wave2 {
			z-index: 5;
			bottom: 0; } } }
