.graphics.services {
	background: linear-gradient(to bottom, $purple 0%, $celeste 40.1%, $purple 100%);

	main {
		position: relative;
		z-index: 10;

		.columns {
			margin-bottom: rem(100px);
			text-align: left;

			h2 {
				margin-bottom: rem(20px);
				border-bottom: rem(1px) solid;
				padding-bottom: rem(5px);
				width: fit-content;
				text-transform: uppercase;
				font-size: rem(20px);
				font-weight: 700;
				text-align: left; }

			.column:nth-child(1) {
				display: flex;
				flex-direction: column;
				justify-content: center;
				padding-right: rem(10px); }

			.column:nth-child(2) {
				padding-left: rem(10px);

				div {
					background-color: $white;
					border-radius: rem(10px);
					height: rem(277px);
					width: 100%;
					box-shadow: 0 0 rem(50px) transparentize($black, .9), 0 0 rem(15px) transparentize($black, .8), 0 0 rem(5px)transparentize($black, .9); } }

			&:nth-child(odd) {
				flex-direction: row-reverse;
				text-align: right;
				.column {
					display: flex;
					flex-direction: column;
					align-items: flex-end; }
				h2 {
					text-align: right; } }

			&:last-child {
				margin-bottom: rem(200px); } } }

	.waves {
		img {
			z-index: 0;
			&.wave1 {
				top: 18%; }
			&.wave2 {
				top: 65%; }
			&.wave3 {
				bottom: 0; } } } }
