.graphics.portfolio {
	background: linear-gradient(to bottom, $purple 0%, $celeste 40.1%, $purple 100%);

	main {

		h1 {
			margin-bottom: rem(50px); }

		span {
			cursor: pointer;
			&:focus, &:visited, &:hover, &:active {
				color: transparentize($white, .5); } }

		.filter-active {
			color: $white;
			text-decoration: underline; }

		.columnHide {
			display: none;
			padding: rem(5px); }

		.columnShow {
			display: block;
			padding: rem(5px); }

		#filters {
			text-align: center;
			margin-bottom: rem(70px); }

		.columns {
			.column {
				padding: rem(5px);
				@for $i from 1 through $work-samples {
					&:nth-child(#{$i}) {
						.categories {
							background-image: url(../media/img/graphics-work#{$i}.svg);
							&:hover {
								background-image: url(../media/img/graphics-work#{$i}-hover.svg); } } } }
				.categories {
					display: flex;
					padding: rem(10px);
					cursor: pointer;
					justify-content: flex-end;
					align-items: flex-end;
					height: calc(((100vw - 1.875rem) / 100 * 80) / 4 - 0.625rem);
					border-radius: rem(10px);
					box-shadow: 0 rem(4px) rem(4px) transparentize($black, .75), 0 rem(2px) rem(2px) transparentize($black, .85), 0 rem(5px) rem(20px) transparentize($black, .7);
					background-size: cover;
					transition: background-image .15s ease;
					p {
						text-transform: uppercase;
						font-size: rem(12px);
						padding: rem(5px);
						border-radius: rem(3px);
						line-height: 1;
						box-shadow: 0 rem(4px) rem(4px) transparentize($black, .75), 0 rem(2px) rem(2px) transparentize($black, .85);
						text-shadow: 0 rem(1px) rem(2px) transparentize($black, .8), 0 rem(1px) rem(2px) transparentize($black, .9);
						margin-right: rem(10px);
						&.print {
							background-color: $print; }
						&.video {
							background-color: $video; }
						&.web {
							background-color: $web; }
						&.ux {
							background-color: $ux; }
						&:last-child {
							margin-right: 0; } } } } } }

	footer {
		margin-top: rem(280px); }

	.modal-sidebar {
		display: none;
		position: fixed;
		justify-content: flex-end;
		align-items: center;
		background-color: $transparent;
		width: 100%;
		height: 100vh;
		top: 0;
		z-index: 100;
		transition: all 1s ease;
		&.display-flex {
			display: flex; }
		&.modal-background-in {
			@include animation (modal-background-in, .5s, 1, ease);
			@include animation-fill-mode (forwards); }
		&.modal-background-out {
			@include animation (modal-background-out, 1s, 1, ease);
			@include animation-fill-mode (forwards); }
		aside {
			position: relative;
			padding: rem(25px);
			border-top-left-radius: rem(20px);
			height: 100%;
			background-color: $white;
			width: rem(450px);
			overflow: auto;
			transition: all .5s cubic-bezier(1, 0, 0, 1);
			&.graphicsPortfolio-modalOpen {
				@include animation (modalOpen, .5s, 1, cubic-bezier(1, 0, 0, 1));
				@include animation-fill-mode (forwards); }
			&.graphicsPortfolio-modalClose {
				@include animation (modalClose, .5s, 1, cubic-bezier(1, 0, 0, 1));
				@include animation-fill-mode (forwards); }
			img.close {
				position: absolute;
				top: rem(10px);
				right: rem(10px);
				transition: all .25s ease;
				&:hover {
					opacity: .5;
					cursor: pointer;
					transform: rotate(90deg); } }
			.categories {
				display: flex;
				flex-direction: row;
				text-transform: uppercase;
				font-size: rem(12px);
				p {
					margin-right: rem(10px);
					color: $white;
					padding: rem(5px);
					border-radius: rem(3px);
					line-height: 1;
					&.print {
						background-color: $print; }
					&.video {
						background-color: $video; }
					&.web {
						background-color: $web; }
					&.ux {
						background-color: $ux; }
					&:last-child {
						margin-right: 0; } } }
			h2 {
				word-break: break-all;
				margin-top: rem(50px);
				text-align: left;
				color: $black; }
			.description {
				color: $darkBlue;
				word-break: break-word;
				margin-top: rem(20px); }
			a {
				display: block;
				margin-top: rem(50px);
				color: $black;
				text-decoration: underline;
				font-weight: 600;
				transition: all .2s ease;
				width: fit-content;
				&:hover {
					opacity: 0.5; }
				&::after {
					content: '';
					margin-left: rem(10px);
					background: url(../media/img/modal-arrow.svg) no-repeat;
					height: rem(8px);
					width: rem(21px);
					display: inline-block; } }
			.tools {
				display: flex;
				margin-top: rem(20px);
				font-size: rem(24px);
				color: #8f8f8f;
				div {
					margin-right: rem(10px);
					transition: all .15s ease;
					&:last-child {
						margin-right: 0; } }
				.html:hover {
					color: $html; }
				.css:hover {
					color: $css; }
				.sass:hover {
					color: $sass; }
				.bootstrap:hover {
					color: $bootstrap; }
				.javascript:hover {
					color: $js; }
				.github:hover {
					color: $black; }
				.wordpress {
					&:hover {
						color: $wordpress; } }
				.wix:hover {
					color: $wix; }
				.figma, .premiere, .after-effects, .affinity-photo, .affinity-designer, .affinity-publisher, .cinema-4d, .bulma, .jquery, .react, .gitlab, .node-js, .zeplin, .gulp, .contentful {
					.tooltip {
						height: rem(24px);
						filter: grayscale(1);
						transition: all .15s ease;
						&:hover {
							filter: grayscale(0); }
						span {
							transition: none; } } }
				.tooltip {
					position: relative;
					&:hover {
						.tooltip-text {
							visibility: visible; } }
					.tooltip-text {
						font-family: 'Lato', sans-serif;
						text-transform: uppercase;
						visibility: hidden;
						font-size: rem(14px);
						width: fit-content;
						height: fit-content;
						background-color: black;
						color: $white;
						text-align: center;
						border-radius: rem(6px);
						padding: rem(5px);
						position: absolute;
						z-index: 1000;
						bottom: 150%;
						left: -50%;
						transform: translateX(-50%);
						margin-left: 100%;
						line-height: 1;
						box-shadow: 0 rem(2px) rem(4px) transparentize($black, .75), 0 rem(2px) rem(2px) transparentize($black, .85);
						&:after {
							content: '';
							position: absolute;
							top: 100%;
							left: 50%;
							margin-left: rem(-5px);
							border-width: rem(5px);
							border-style: solid;
							border-color: $black $transparent $transparent $transparent; } } } } } }

	.waves {
		.wave1 {
			bottom: 0;
			z-index: -1; }
		.wave2 {
			bottom: 0; } } }
