.audio footer {

	transition: all .25s;
	color: $white;
	background-color: $grey;

	.container {
		display: flex;
		flex-direction: column;
		align-items: center; }

	a {
		&:hover {
			opacity: .5; } }

	.footer-top,
	.footer-bottom {
		font-weight: 400; }
	.footer-top {
		padding: rem(40px) 0 rem(20px) 0;
		text-align: center;
		.footer-icons {
			display: flex;
			justify-content: center;
			margin-bottom: rem(20px);
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				background-color: $white;
				border-radius: 100%;
				color: $grey;
				height: rem(40px);
				width: rem(40px);
				margin: 0 rem(5px);
				i {
					font-size: rem(20px); } } }
		.footer-top-info {
			margin: 0 auto;
			div {
				letter-spacing: .5px;
				font-weight: 300; }
			p {
				margin-bottom: rem(5px);
				font-weight: 400;
				&:last-child {
					margin-bottom: 0; } } } }
	.footer-divider {
		background-color: transparentize($white, .8);
		width: rem(50px);
		height: rem(1px); }
	.footer-bottom {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: rem(20px) 0 rem(40px) 0;
		a {
			display: block;
			font-size: rem(12px);
			margin-top: rem(20px);
			border-bottom: rem(1px) solid $white; } } }


